.section::-webkit-scrollbar,
.menulist-container::-webkit-scrollbar {
  /* display: none; */
}
.section,
.menulist-container {
  /* -ms-overflow-style: none; */
  /* scrollbar-width: none; */
}

.section {
  width: 100%;
  height: 100%;
  position: relative;
  padding: 0.2rem 0;
  border: 1px solid rgba(255, 255, 255, 0.5);
  border-radius: 0.25rem;
  overflow: hidden;
}
.menulist-container {
  width: 100%;
  height: 100%;
  overflow: auto;
}
.menulist-center {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  grid-auto-rows: 250px;
  gap: 0.2rem;
  padding: 0.125rem;
  margin: 0 0.2rem;
}

@media screen and (max-width: 479px) {
  .menulist-center {
    grid-template-columns: repeat(2, 1fr);
    grid-auto-rows: minmax(210px, 230px);
    gap: 0.15rem;
    margin: 0 0.125rem;
    /* border: 1px solid red; */
  }
}
@media screen and (min-width: 480px) and (max-width: 767px) {
  .menulist-center {
    grid-template-columns: repeat(2, 1fr);
    grid-auto-rows: minmax(230px, 250px);
    gap: 0.2rem;
    margin: 0 0.2rem;
  }
}
@media screen and (min-width: 768px) and (max-width: 1023px) {
  .menulist-center {
    grid-template-columns: repeat(3, 1fr);
    grid-auto-rows: minmax(230px, 250px);
    margin: 0 0.2rem;
    gap: 0.2rem;
  }
}
@media screen and (min-width: 1024px) and (max-width: 1279px) {
  .menulist-center {
    grid-template-columns: repeat(4, 1fr);
    grid-auto-rows: minmax(250px, 280px);
    margin: 0 0.25rem;
    gap: 0.25rem;
  }
}
@media screen and (min-width: 1280px) and (max-width: 1439px) {
  .menulist-center {
    grid-template-columns: repeat(5, 1fr);
    grid-auto-rows: minmax(280px, 310px);
    margin: 0 0.3rem;
    gap: 0.275rem;
  }
}
@media screen and (min-width: 1440px) {
  .menulist-center {
    grid-template-columns: repeat(5, 1fr);
    grid-auto-rows: minmax(310px, 340px);
    margin: 0 0.35rem;
    gap: 0.3rem;
  }
}
