.order-modal-container {
  display: none;
}

.show-order-modal-container {
  display: block;
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: rgba(22, 8, 8, 0.8);
}

.order-modal-content {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  /* width: 70%; */
  /* height: 80%; */
  width: 80%;
  height: 70%;
  max-width: 800px;
  max-height: 600px;
  background-color: #4d4d4d;
  padding: 0.5rem;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  border-radius: 0.25rem;
}

.order-title {
  flex: 0 0 60px;
  padding: 0.125rem 0.5rem;
  display: flex;
  align-items: center;
  background-color: #000;
  color: yellow;
}
.order-title h2 {
  font-size: 1.5rem;
}
.order-title p {
  margin-left: 2rem;
  font-size: 1.3rem;
}

.order-message {
  font-size: 1.2rem;
  padding: 0.5rem;
  display: flex;
  align-items: center;
  background-color: #fff;
  font-weight: bold;
}

.order-items-list {
  flex: 1;
  overflow: auto;
  padding: 0.5rem;
  background-color: #fff;
}
.order-item-container {
  display: flex;
  flex-direction: column;
  margin: 0.25rem 0;
  padding: 1rem 0.5rem;
  border-top: 2px solid #4d4d4d;
  border-bottom: 2px solid #4d4d4d;
}
.order-item-details {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-weight: bold;
}
.order-item-details p,
.order-item-details span {
  font-size: 1rem;
  font-weight: bold;
}

.order-summary {
  flex: 0 0 50px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #000;
}
.order-total-info {
  flex: 1;
  display: flex;
  justify-content: center;
  justify-content: space-between;
  align-items: center;
  font-size: 1.2rem;
  font-weight: bold;
  color: yellow;
  padding: 0.25rem 1rem;
}
.close-order-modal {
  flex: 0 0 100px;
  height: 45px;
  font-size: 1rem;
  font-weight: bold;
  display: flex;
  justify-content: center;
  align-items: center;
  line-height: 1;
  color: #000;
  background-color: #fff;
  border-color: transparent;
  margin-right: 0.25rem;
  cursor: pointer;
}

.close-order-modal:hover {
  /* color: #fff; */
  /* background-color: #4d4d4d; */
}
.close-order-modal .icon {
  margin-right: 8px;
  font-size: 1.2rem;
}

@media screen and (max-width: 479px) {
  .order-modal-content {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    transform: translateY(90px);
    max-width: 800px;
    max-height: calc(100% - 90px);
    padding: 0.25rem;
  }

  .order-title {
    flex: 0 0 60px;
    /* padding: 0.125rem 1rem; */
    padding: 0.125rem 0.2rem;
    padding-left: 0.5rem;
    margin-bottom: 0.25rem;
    /* border: 1px solid red; */
  }

  .order-title h2 {
    /* font-size: 1.4rem; */
    font-size: 1.3rem;
  }
  .order-title p {
    margin-left: 1.5rem;
    /* font-size: 1.2rem; */
    font-size: 1.1rem;
  }

  .order-message {
    /* font-size: 1.4rem; */
    font-size: 1.3rem;
    padding: 1rem 0.5rem;
    /* margin-bottom: 0.25rem; */
    /* border: 1px solid red; */
  }
  .order-items-list {
    /* padding: 0.75rem; */
    padding: 0.5rem;
    margin-bottom: 0.25rem;
    /* border: 1px solid red; */
  }
  .order-item-container {
    margin: 0.15rem 0 0.3rem;
    padding: 0.7rem 0.25rem;
    /* margin: 0.3rem; */
    /* padding: 0.75rem; */
    /* border: 1px solid red; */
  }
  .order-item-details p,
  .order-item-details span {
    font-size: 1rem;
    padding: 0.125rem;
  }
  .order-summary {
    flex: 0 0 50px;
    border-bottom-left-radius: 0.25rem;
    border-bottom-right-radius: 0.25rem;
  }
  .order-total-info {
    font-size: 1.1rem;
    padding: 0.25rem 0.5rem;
    margin: 0 0.25rem;
  }
  .close-order-modal {
    height: 100%;
    font-size: 1rem;
    margin-right: 0;
    border-bottom-right-radius: 0.25rem;
  }
  .close-order-modal .icon {
    margin-right: 6px;
    font-size: 1.2rem;
  }
}
@media screen and (min-width: 480px) and (max-width: 767px) {
  .order-modal-content {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    transform: translateY(90px);
    max-width: 800px;
    max-height: calc(100% - 90px);
    padding: 0.35rem;
  }
  .order-title {
    flex: 0 0 60px;
    /* padding: 0.125rem 0.2rem; */
    padding: 0.25rem 1rem;
    margin-bottom: 0.25rem;
  }
  .order-title h2 {
    font-size: 1.4rem;
  }
  .order-title p {
    margin-left: 1.5rem;
    font-size: 1.2rem;
  }
  .order-message {
    font-size: 1.4rem;
    padding: 1rem;
  }
  .order-items-list {
    padding: 0.6rem;
    margin-bottom: 0.25rem;
  }
  .order-item-container {
    margin: 0.3rem;
    padding: 0.75rem 0.5rem;
  }
  .order-item-details p,
  .order-item-details span {
    font-size: 1.1rem;
    padding: 0.125rem;
  }
  .order-summary {
    flex: 0 0 50px;
    border-bottom-left-radius: 0.25rem;
    border-bottom-right-radius: 0.25rem;
  }
  .order-total-info {
    font-size: 1.2rem;
    padding: 0.25rem 0.5rem;
    margin: 0 0.5rem;
  }
  .close-order-modal {
    height: 100%;
    font-size: 1.1rem;
    margin-right: 0;
    border-bottom-right-radius: 0.25rem;
  }
  .close-order-modal .icon {
    margin-right: 8px;
    font-size: 1.3rem;
  }
}
@media screen and (min-width: 768px) and (max-width: 1023px) {
  .order-modal-content {
    width: 75%;
    height: 70%;
  }
  .order-title h2 {
    font-size: 1.4rem;
  }
  .order-title p {
    font-size: 1.3rem;
  }
  .order-message {
    font-size: 1.2rem;
  }
}
@media screen and (min-width: 1024px) and (max-width: 1279px) {
  .order-modal-content {
    width: 70%;
    height: 65%;
  }
}
@media screen and (min-width: 1280px) and (max-width: 1439px) {
  .order-modal-content {
    width: 60%;
    height: 60%;
  }
}
@media screen and (min-width: 1440px) {
  .order-modal-content {
    width: 50%;
    height: 50%;
  }
}
