.modal-container {
  display: none;
}
.show-modal-container {
  display: block;
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: rgba(22, 8, 8, 0.8);
}

.modal-content {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 70%;
  height: 80%;
  max-height: 700px;
  max-height: 800px;
  padding: 0.5rem;
  display: flex;
  flex-direction: column;
  background-color: #4d4d4d;
  border-radius: 0.25rem;
}
.modal-content h2 {
  flex: 0 0 70px;
  display: flex;
  align-items: center;
  margin-bottom: 0.35rem;
  padding: 0.125rem 0.8rem;
  font-size: 1.5rem;
  background-color: #000;
  color: yellow;
}
.modal-items-list {
  flex: 1;
  display: flex;
  margin-bottom: 0.25rem;
  padding: 0.25rem;
  overflow: hidden;
  border: 1px solid rgba(255, 255, 255, 0.5);
}
.modal-item-container {
  display: flex;
  flex-direction: column;
  width: 100%;
  padding: 0.125rem;
}
.modal-item-details {
  flex: 1;
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  grid-template-rows: repeat(5, 1fr);
  gap: 0.3rem;
  padding: 0.2rem;
  overflow: hidden;
  margin-bottom: 0.25rem;
  background-color: #fafafa;
}

.modal-menuItem {
  grid-column: 1 / 3;
  grid-row: 1 / 6;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 0.25rem;
  background-color: #b0b0b0;
}
.modal-menuItem img {
  width: 100%;
  height: 100%;
  border-radius: 0.5rem;
}
.modal-info {
  grid-column: 3 / 6;
  grid-row: 1 / 3;
  display: flex;
  flex-direction: column;
  border-radius: 0.25rem;
  margin: 0 0.25rem;
  border: 1px solid rgba(255, 255, 255, 0.5);
  overflow: hidden;
}
.modal-item-title {
  /* flex: 0 1 60%; */
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0.3rem;
}
.modal-item-title h3 {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0.25rem;
  font-size: 1.3rem;
  color: #000;
  line-height: 1;
}
.modal-item-title span {
  color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0.2rem;
  font-size: 1.125rem;
  line-height: 1;
  color: #000;
}
.drink-type {
  /* flex: 0 1 40%; */
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0.25rem;
  overflow: hidden;
}

.drink-type .hot-type,
.drink-type .iced-type {
  margin: 0.75rem;
  padding: 0.35rem 1rem;
  width: auto;
  height: auto;
  background-color: transparent;
  border-color: transparent;
  text-transform: uppercase;
  display: flex;
  justify-content: center;
  align-items: center;
  letter-spacing: 0.05rem;
  border-radius: 0.5rem;
  color: #b0b0b0;
  font-weight: bold;
  font-size: 1rem;
  cursor: pointer;
  line-height: 1;
  border: 1px solid rgb(176, 176, 176);
}

.hot-type:hover,
.iced-type:hover {
  color: #000;
  border: 1px solid rgba(0, 0, 0, 0.5);
}

.active-hot.hot-type {
  color: #000;
  border: 1px solid rgba(0, 0, 0, 0.5);
}
.active-iced.iced-type {
  color: #000;
  border: 1px solid rgba(0, 0, 0, 0.8);
}

.selected-items-info {
  grid-column: 3 /6;
  grid-row: 3 / 6;
  overflow: auto;
  border-radius: 0.25rem;
  border: 1px solid rgba(255, 255, 255, 0.5);
}
.items-info {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 0.25rem;
  margin: 0 0.25rem;
}

.size-selection-row,
.quantity-selection-row,
.coffee-row {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 0.3rem;
  margin-bottom: 0.3rem;
  font-weight: bold;
  border-bottom: 1px solid rgba(176, 176, 176, 0.5);
}
.size-selection-row h4,
.quantity-selection-row h4,
.coffee-row h4 {
  font-size: 1.1rem;
  margin: 0.3rem 0;
  padding: 0.3rem;
  color: #000;
  line-height: 1;
}
.btn-row {
  width: 100%;
  padding: 0.25rem;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}
.btn-row .coffee-size {
  flex: 1;
  margin: 0 0.2rem;
  padding: 0.25rem 0.5rem;
  background-color: transparent;
  border-color: transparent;
  color: #b0b0b0;
  font-size: 1.1rem;
  text-transform: capitalize;
  cursor: pointer;
}
.btn-row .active-size {
  color: #000;
}
.btn-row .minus,
.btn-row .plus {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0.1rem;
  font-size: 1.1rem;
  background-color: transparent;
  border-color: transparent;
  color: #000;
  font-weight: bold;
  cursor: pointer;
}
.btn-row span {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.1rem;
  color: #000;
}
.coffee-row-header p {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0.25rem 0;
  padding: 0.25rem;
  font-size: 0.875rem;
  color: #000;
  line-height: 1;
}

.coffee-row-header {
  display: flex;
  justify-content: center;
  align-items: center;
}
.item-total {
  flex: 0 0 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: yellow;
  background-color: #000;
  font-weight: bold;
  letter-spacing: 0.08rem;
  font-size: 1.2rem;
  border-top: 1px solid rgba(255, 255, 255, 0.5);
}

.order-btn {
  flex: 0 0 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #4d4d4d;
}
.order-btn span {
  line-height: 1;
}
.close-modal-btn,
.add-cart-btn {
  height: 100%;
  font-weight: bold;
  background-color: transparent;
  border-color: transparent;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.2rem;
  cursor: pointer;
  color: #000;
}

.close-modal-btn {
  flex: 0 35%;
  background-color: #b0b0b0;
  color: #000;
  border-bottom-left-radius: 0.25rem;
}
.add-cart-btn {
  flex: 1;
  background-color: #ffeb3b;
  color: #000;
  border-bottom-right-radius: 0.25rem;
}
.close-modal-btn .icon,
.add-cart-btn .icon {
  margin-right: 8px;
  font-size: 1.3rem;
  line-height: 1;
}
.close-modal-btn span,
.add-cart-btn span {
  line-height: 1;
}
.close-modal-btn:hover {
  background-color: #757575;
  color: #fff;
}
.add-cart-btn:hover {
  background-color: #fbc02d;
  color: #fff;
}

