footer {
  overflow: hidden;
}
.addItem-container {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  /* padding-right: 1rem; */
  /* padding-left: 1rem; */
  /* border: 1px solid red; */
}

.addItem-center {
  display: flex;
  align-items: center;
  justify-content: center;
}

.call-btn,
.left-btn,
.right-btn {
  margin: 0 0.5rem;
  width: 120px;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1rem;
  font-weight: bold;
  background-color: #fff;
  border-color: transparent;
  color: #000;
  border-radius: 0.25rem;
  cursor: pointer;
}
.call-btn .icon,
.left-btn .icon,
.right-btn .icon {
  font-size: 1.2rem;
  margin-right: 8px;
}
.addItem-container span {
  line-height: 1;
}
.cart-order {
  position: relative;
}
.cart-amount {
  position: absolute;
  top: -5px;
  right: -5px;
  background-color: #f44336;
  color: #fff;
  font-size: 0.8rem;
  font-weight: bold;
  width: 20px;
  height: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
}

.shake {
  background-color: #f44336;
  color: #fff;
  animation: shake 0.5s linear;
  animation-iteration-count: 4;
}
@keyframes shake {
  0%,
  100% {
    transform: translateX(0);
  }
  20%,
  60% {
    transform: translateX(-2px);
  }
  40%,
  80% {
    transform: translateX(2px);
  }
}
@media screen and (max-width: 479px) {
  footer {
    flex: 0 0 50px;
    /* margin: 0.25rem; */
    /* overflow: hidden; */
  }

  .addItem-container {
    flex: 1;
    padding-right: 0;
    padding-left: 0;
    padding: 0 0.25rem;
    /* border: 1px solid red; */
  }
  .call-btn {
    width: auto;
    margin-left: 0;
    padding: 0.5rem;
    font-size: 0.8rem;
  }

  .left-btn,
  .right-btn {
    width: auto;
    margin-right: 0;
    padding: 0.5rem;
    font-size: 0.8rem;
  }

  .call-btn .icon,
  .left-btn .icon,
  .right-btn .icon {
    margin-right: 4px;
    font-size: 1rem;
  }

  .cart-amount {
    top: -1px;
    right: -1px;
    font-size: 0.4rem;
    width: 16px;
    height: 16px;
  }
}
