.category-btns::-webkit-scrollbar {
  display: none;
}

.category-btns {
  -ms-overflow-style: none; /* 인터넷 익스플로러 */
  scrollbar-width: none; /* 파이어폭스 */
}

.category {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 0.125rem;
}

.sidebar-container {
  flex: 1;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  /* overflow: auto; */
  /* border: 1px solid red; */
}
.category-title {
  flex: 0 0 60px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 0.25rem;
  color: #fff;
  font-size: 1.3rem;
  font-weight: bold;
  letter-spacing: 0.25rem;
}
.category-btns {
  flex: 1;
  display: flex;
  flex-direction: column;
  overflow: auto;
}
.filter-btn {
  flex: 0 0 50px;
  margin-bottom: 0.25rem;
  cursor: pointer;
  color: #fff;
  border-radius: 0.25rem;
  font-size: 1rem;
  font-weight: bold;
  background-color: darkorange;
  border-color: transparent;
  letter-spacing: 0.25rem;
}

.active-category,
.filter-btn:hover {
  background-color: yellow;
  color: #000;
}

@media screen and (max-width: 479px) {
  .category {
    display: flex;
    flex-direction: row;
    align-items: center;
    overflow: visible;
    margin-bottom: 0.25rem;
    padding: 0.125rem 0;
    /* border: 1px solid red; */
  }

  .sidebar-container {
    flex-direction: row;
  }

  .category-title {
    display: none;
  }

  .category-btns {
    height: 34px;
    flex-direction: row;
  }

  .filter-btn {
    flex: 1 auto;
    margin: 0 2px;
    font-size: 0.85rem;
    letter-spacing: 0;
  }
}
@media screen and (min-width: 480px) and (max-width: 767px) {
  .category {
    display: flex;
    flex-direction: row;
    align-items: center;
    overflow: visible;
    margin-bottom: 0.25rem;
    padding: 0.125rem 0;
  }
  .sidebar-container {
    flex-direction: row;
  }

  .category-title {
    display: none;
  }

  .category-btns {
    height: 36px;
    flex-direction: row;
  }

  .filter-btn {
    flex: 1 auto;
    margin: 0 2px;
    font-size: 0.95rem;
    letter-spacing: 0;
  }
}
@media screen and (min-width: 768px) and (max-width: 1023px) {
  .category {
    padding: 0.125rem;
  }
  .category-title {
    flex: 0 0 60px;
    margin-bottom: 0.25rem;
    font-size: 1.3rem;
    letter-spacing: 0.25rem;
  }

  .filter-btn {
    flex: 0 0 50px;
    margin-bottom: 0.25rem;
    font-size: 1.1rem;
  }
}
@media screen and (min-width: 1024px) and (max-width: 1279px) {
  .category {
    padding: 0.2rem;
  }
  .category-title {
    flex: 0 0 70px;
    margin-bottom: 0.3rem;
    font-size: 1.4rem;
    letter-spacing: 0.3rem;
  }

  .filter-btn {
    flex: 0 0 55px;
    margin-bottom: 0.3rem;
    font-size: 1.2rem;
  }
}
@media screen and (min-width: 1280px) and (max-width: 1439px) {
  .category {
    padding: 0.25rem;
  }
  .category-title {
    flex: 0 0 80px;
    margin-bottom: 0.35rem;
    font-size: 1.5rem;
    letter-spacing: 0.35rem;
  }

  .filter-btn {
    flex: 0 0 60px;
    margin-bottom: 0.35rem;
    font-size: 1.3rem;
  }
}
@media screen and (min-width: 1440px) {
  .category {
    padding: 0.3rem;
  }
  .category-title {
    flex: 0 0 90px;
    margin-bottom: 0.4rem;
    font-size: 1.6rem;
    letter-spacing: 0.4rem;
  }

  .filter-btn {
    flex: 0 0 70px;
    margin-bottom: 0.4rem;
    font-size: 1.4rem;
  }
}
