nav {
  padding: 0.125rem;
  display: flex;
}
.nav-container {
  flex: 1;
  display: flex;
  justify-content: space-between;
  height: 100%;
}
.nav-center {
  flex: 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: 1.5rem;
}
.nav-center > h2 {
  color: #fff;
  text-transform: capitalize;
  font-size: 1.8rem;
}
.nav-number-center {
  flex: 0 0 70px;
  height: 50%;
  margin: auto 0;
  /* margin: auto; */
  padding: 0.5rem;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: yellow;
  color: #000;
  /* font-size: 1.2rem; */
  font-weight: bold;
}
.nav-number-center .icon {
  font-size: 1.3rem;
  margin-right: 8px;
  line-height: 1;
}
.nav-number-table {
  font-size: 1.1rem;
  line-height: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  /* border: 1px solid red; */
}
@media screen and (max-width: 479px) {
  nav {
    flex: 0 0 90px;
    padding: 0.25rem;
    margin-bottom: 0.2rem;
    /* border: 1px solid red; */
  }

  .nav-container {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0.15rem;
  }
  .nav-center {
    flex: 0 auto;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 0;
  }

  .main-center h2 {
    font-size: 1.8rem;
  }
  .nav-number-center {
    position: absolute;
    top: 50%;
    right: 0;
    transform: translateY(-50%);
    height: 70%;
    margin-right: 0.2rem;
    padding: 0.5rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: yellow;
  }
  .nav-number-center .icon {
    font-size: 1.1rem;
    margin: 0 auto;
    margin-bottom: 0.2rem;
  }
  .nav-number-table {
    font-size: 1rem;
    margin: 0 auto;
  }
}
