.details {
  display: none;
}

.show-details {
  display: block;
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: rgba(22, 8, 8, 0.8);
}

.details-modal-content {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  /* width: 70%; */
  /* height: 80%; */
  width: 80%;
  height: 70%;
  max-width: 800px;
  max-height: 600px;
  background-color: #4d4d4d;
  padding: 0.5rem;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  border-radius: 0.25rem;
}

.details-title {
  flex: 0 0 60px;
  padding: 0.125rem 0.5rem;
  display: flex;
  align-items: center;
  background-color: #000;
  color: yellow;
}
.details-title h2 {
  font-size: 1.5rem;
}

.details-items-list {
  flex: 1;
  padding: 0.5rem;
  background-color: #fff;
  overflow: auto;
}

.details-item-container {
  display: flex;
  flex-direction: column;
  margin: 0.25rem 0;
  padding: 1rem 0.5rem;
  border-top: 2px solid #4d4d4d;
  border-bottom: 2px solid #4d4d4d;
}

.details-item-info {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-weight: bold;
  margin-bottom: 0.25rem;
}
.details-item-info p,
.details-item-info span {
  font-size: 1rem;
  font-weight: bold;
}

.toggle-options-btn {
  background-color: transparent;
  border-color: transparent;
  color: #000;
  font-size: 0.9rem;
  font-weight: bold;
  width: 100%;
  display: flex;
  align-items: center;
  cursor: pointer;
  margin-bottom: 0.25rem;
}
.options-container {
  display: flex;
  flex-direction: column;
  gap: 0.2rem;
  padding: 0.25rem 0.5rem;
  font-weight: bold;
  font-size: 0.9rem;
  text-transform: capitalize;
  border: 1px solid black;
  border-radius: 0.25rem;
}
.option-item {
  display: flex;
  justify-content: space-between;
}

.option-detail {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.125rem 0;
}
.details-summary {
  display: flex;
  flex-direction: column;
  justify-content: center;
  font-weight: bold;
  background-color: #000;
}

.details-total-price,
.details-total-info {
  font-size: 1.2rem;
  color: yellow;
  padding: 0.35rem 0.25rem;
  display: flex;
  justify-content: center;
  align-items: center;
}
.details-total-price span,
.details-total-info span {
  margin: 0 0.25rem;
  width: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.2rem;
}

.close-details {
  flex: 0 0 45px;
  font-size: 1rem;
  font-weight: bold;
  display: flex;
  justify-content: center;
  align-items: center;
  line-height: 1;
  background-color: #fff;
  border-color: transparent;
  color: #000;
  cursor: pointer;
}
.close-details:hover {
  /* color: #fff; */
  /* background-color: #4d4d4d; */
}
.close-details .icon {
  margin-right: 8px;
  font-size: 1.2rem;
}

@media screen and (max-width: 479px) {
  .details-modal-content {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    transform: translateY(90px);
    max-width: 800px;
    max-height: calc(100% - 90px);
    padding: 0.25rem;
  }
  .details-title {
    flex: 0 0 60px;
    padding: 0.125rem 0.2rem;
    padding-left: 0.5rem;
    margin-bottom: 0.25rem;
  }
  .details-title h2 {
    font-size: 1.3rem;
  }
  .details-items-list {
    padding: 0.5rem;
    margin-bottom: 0.25rem;
  }
  .details-item-container {
    margin: 0.15rem 0 0.3rem;
    padding: 0.7rem 0.25rem;
  }
  .details-item-info p,
  .details-item-info span {
    font-size: 1rem;
    padding: 0.125rem;
  }
  .toggle-options-btn {
    font-size: 0.9rem;
    padding: 0.125rem 0;
    margin-bottom: 0.25rem;
  }
  .options-container {
    gap: 0.2rem;
    padding: 0.25rem 0.5rem;
    font-size: 0.9rem;
    border-radius: 0.25rem;
  }
  .option-detail {
    padding: 0.125rem 0;
  }
  .details-total-price,
  .details-total-info {
    padding: 0.25rem 0.5rem;
  }
  .details-total-price span,
  .details-total-info span {
    width: 120px;
    padding: 0.25rem;
    font-size: 1.1rem;
  }
  .close-details {
    flex: 0 0 50px;
    font-size: 1rem;
    border-bottom-left-radius: 0.25rem;
    border-bottom-right-radius: 0.25rem;
  }
  .close-details .icon {
    margin-right: 6px;
    font-size: 1.2rem;
  }
}

@media screen and (min-width: 480px) and (max-width: 767px) {
  .details-modal-content {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    transform: translateY(90px);
    max-width: 800px;
    max-height: calc(100% - 90px);
    padding: 0.35rem;
  }
  .details-title {
    flex: 0 0 60px;
    padding: 0.25rem 1rem;
    margin-bottom: 0.25rem;
  }
  .details-title h2 {
    font-size: 1.4rem;
  }
  .details-items-list {
    padding: 0.6rem;
    margin-bottom: 0.25rem;
  }
  .details-item-container {
    margin: 0.3rem;
    padding: 0.75rem 0.5rem;
  }
  .details-item-info p,
  .details-item-info span {
    font-size: 1.1rem;
    padding: 0.125rem;
  }
  .toggle-options-btn {
    font-size: 1rem;
    padding: 0.125rem 0;
    margin-bottom: 0.25rem;
  }
  .options-container {
    gap: 0.3rem;
    padding: 0.25rem 0.5rem;
    font-size: 1rem;
    border-radius: 0.25rem;
  }
  .option-detail {
    padding: 0.13rem 0;
    /* border: 1px solid red; */
  }
  .details-total-info {
    font-size: 1.2rem;
    padding: 0.35rem;
    /* border: 1px solid red; */
  }
  .details-total-price,
  .details-total-info {
    padding: 0.25rem 0.5rem;
  }
  .details-total-price span,
  .details-total-info span {
    width: 140px;
    padding: 0.3rem;
    font-size: 1.2rem;
  }

  .close-details {
    flex: 0 0 50px;
    font-size: 1.1rem;
    border-bottom-left-radius: 0.25rem;
    border-bottom-right-radius: 0.25rem;
  }
  .close-details .icon {
    margin-right: 8px;
    font-size: 1.3rem;
  }
}
